<template>
    <div class="alert-window">
        
        <transition-group name="fade">
            <v-alert
                v-for="(index, i) in indexes"
                :key="index"
                :type="types[i]"
                class="single-alert text-wrap"
                elevation="5"
                dense
                shaped
                :dismissible="dismissibles[i]"
                @input="closeBtnClick(i)"
            >
                {{ messages[i] }}
            </v-alert>
        </transition-group>
    
    </div>
</template>

<script>

    import { mapActions, mapState } from 'vuex';

    export default {
        name: 'Alert',

        data() {
            return {
                lastIndex: null
            }
        },

        computed: {
            ...mapState('alert', [
                'indexes',
                'messages',
                'types',
                'timeouts',
                'dismissibles'
            ])
        },

        watch: {
            indexes: function (val) {
                let i = val.length - 1;
                
                if (this.lastIndex === null || this.lastIndex < val[i]) {
                    if (!this.dismissibles[i])
                        window.setTimeout(this.removeAlert, this.timeouts[i], val[val.length - 1]);
                    
                    this.lastIndex = i;
                }
            }
        },

        methods: {
            ...mapActions('alert', [
                'removeAlert'
            ]),

            closeBtnClick(i) {
                this.removeAlert(i);
            }
        }

    }

</script>

<style scoped>

    .alert-window {
        z-index: 10;
        text-align: center;
        position: fixed;
        width: 100%;
    }

    .single-alert {
        max-width: fit-content;
        white-space: nowrap;
        margin: 10px auto;
    }

    @keyframes wobble {
        0%   { transform: translateY(-60px); opacity: 0; }
        50%  { transform: translateY(0px); opacity: 1; }
        60%  { transform: translateX(8px); }
        70%  { transform: translateX(-8px); }
        80%  { transform: translateX(4px); }
        90%  { transform: translateX(-4px); }
        100% { transform: translateX(0px); }
    }

    .fade-enter-active {
        animation: wobble 0.6s ease;
    }

    .fade-leave {
        opacity: 1;
    }

    .fade-leave-to {
        opacity: 0;
    }

    .fade-leave-active {
        transition: all 0.4s ease;
        position: absolute;
    }

    .fade-move {
        transition: all 0.5s ease;
    }

</style>